import m from 'mithril';
import moment from 'moment';

const MinutesInfo = {
    view(ctrl, info) {
        return <section>
            <div className='row'>
                <div className='col-xs-6'>
                    <p><b>:</b>{info.day() || ''}</p>
                    <p><b>:</b>{info.where() || ''}</p>
                </div>
                <div className='col-xs-6'>
                    <p><b>:</b>{`${info.startTime() || ''}  ${info.endTime() || ''}`}</p>
                    <p><b>:</b>{info.secretary() || ''}</p>
                </div>
            </div>
            <p><b>:</b>
                {(() => {
                    if(info.entryList.length) {
                        return info.entryList.map(item =>  '' + item());
                    } else {
                        return '';
                    }
                })()}
            </p>
        </section>;
    }
};

export default MinutesInfo;
